<template>
    <div class='row'>
        <div class='col-md-12'>
            <div class='card'>
                <div class='card-body'>
                    <h4 class="card-title">Filters</h4>
                </div>
                <div class='card-body'>
                    <h3>From</h3>
                    <div class='row'>
                        <div class='col-md-6'>
                            <select class='form-control' v-model="fyear">
                                <option v-for="(year,fy) in years" :key="fy">{{year}}</option>
                            </select>
                        </div>
                        <div class='col-md-6'>
                            <select class='form-control' v-model="fmonth">
                                <option v-for="(monthf,fm) in months" :key="fm" :value="monthf.id">{{monthf.name}}</option>
                            </select>
                        </div>
                    </div>
                    <hr>
                    <h3>To</h3>
                    <div class='row'>
                        <div class='col-md-6'>
                            <select class='form-control' v-model="tyear">
                                <option v-for="(yeart,ty) in years" :key="ty">{{yeart}}</option>
                            </select>
                        </div>
                        <div class='col-md-6'>
                            <select class='form-control' v-model="tmonth">
                                <option v-for="(montht,tm) in months" :key="tm" :value="montht.id">{{montht.name}}</option>
                            </select>
                        </div>
                    </div>
                </div>
              <div class="clearfix">&nbsp;
                  <hr>
              </div>
                <div class="card-body">
                  <data-table :loading='loading' v-on:buttonEvent='actionHandler' v-on:event='pages' :actions='actions' :resource='resource' :pagination='pagination' :title='title' :sub-title='subTitle' :data='data' :columns='columns'>
            </data-table>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
const resource = 'payments'
export default {
  components: {},
  data () {
    return {
      pagination: {},
      resource: resource,
      loading: true,
      title: 'Payments',
      subTitle: 'All the payments in the system',
      columns: [
        {
          name: 'Date Paid',
          type: 'code'
        },
        {
          name: 'Narration',
          type: 'story'
        },
        {
          name: 'Job',
          type: ''
        },
        {
          name: 'Method',
          type: 'badge'
        },
        {
          name: 'Amount',
          type: 'currency'
        }
      ],
      actions: [
        {
          name: '',
          icon: 'icon-print',
          type: 'link',
          path: 'print-payment',
          design: 'success'
        }
      ],
      data: [],
      fmonth: 'Jan',
      tmonth: 'Dec',
      fyear: '2018',
      tyear: '2018',
      years: ['2017', '2018', '2019'],
      months: [
        { id: 'Jan', name: 'January' },
        { id: 'Feb', name: 'February' },
        { id: 'Mar', name: 'March' },
        { id: 'Apr', name: 'April' },
        { id: 'May', name: 'May' },
        { id: 'Jun', name: 'June' },
        { id: 'Jul', name: 'July' },
        { id: 'Aug', name: 'August' },
        { id: 'Sep', name: 'September' },
        { id: 'Oct', name: 'October' },
        { id: 'Nov', name: 'November' },
        { id: 'Dec', name: 'December' }
      ]
    }
  },
  created () {
    this.pages(10, 0)
  },
  methods: {
    pages: function (itemsPerPage, pageNumber) {
      this.loading = true
      var dateLastMonth = this.fmonth + '-' + this.fyear
      var dateNextMonth = this.tmonth + '-' + this.tyear
      this.subTitle = `All payments made between ${dateLastMonth} and ${dateNextMonth}`
      this.$http
        .get(
          this.resource + '/pages/' + itemsPerPage + '/from/' + dateLastMonth + '/to/' + dateNextMonth + '?page=' + pageNumber + '')
        .then(result => {
          var info = result.data.data
          this.data = []
          info.forEach(element => {
            var item = {
              id: element.id,
              job: element.project.title + ' - ' + element.client.name,
              amount: element.amount,
              balance: element.balance,
              datepaid: element.created_at,
              method: element.method,
              narration: element.narration,
              reference: element.reference
            }
            this.data.push(item)
          })
          this.pagination = this.$makePagination(result.data)
          this.loading = false
        })
        .catch(error => {
          this.$toastr('error', error.message)
          this.loading = false
        })
    },
    actionHandler: function (item, action) {
      switch (action) {
        case 'delete':
          this.$http
            .post(this.resource + '/' + action, item)
            .then(result => {
              this.$toastr('success', 'Success')
              this.pages(10, this.pagination.current)
            })
            .catch(error => {
              this.$toastr('error', error.message)
            })
          break

        default:
          break
      }
    }
  }
}
</script>
<style>
</style>



